@media (min-width: 768px) and (max-width: 1023px) {

  .display-lmobile-36- {
    font-family: $unnamed-font-family-source-serif-pro;
    font-style: $unnamed-font-style-normal;
    font-weight: $unnamed-font-weight-600;
    font-size: $unnamed-font-size-36;
    line-height: $unnamed-line-spacing-45;
    letter-spacing: $unnamed-character-spacing-0;
    color: $unnamed-color-172026;
  }

  .display-m-24-2 {
    font-family: $unnamed-font-family-source-serif-pro;
    font-style: $unnamed-font-style-normal;
    font-weight: $unnamed-font-weight-600;
    font-size: $unnamed-font-size-24;
    line-height: $unnamed-line-spacing-30;
    letter-spacing: $unnamed-character-spacing-0;
    color: $unnamed-color-172026;
  }

  .container {
    width: 90%;
    margin: auto;
  }

  /* ==============================Header 2===================================*/

  header {
    .nav-links {

      position: absolute;
      top: 9vh;
      height: 91vh;
      width: 100vw;
      left: 0;
      background-color: white;
      display: flex;
      flex-direction: column;
      align-items: start;
      justify-content: start;
      z-index: 3;
      transform: translateY(-140%);
      transition: transform 0.5s ease-in;
      padding: 0;
      color: black;

      a {
        color: black;
      }
    }

    li {
      padding: 0.75em;


    }

    .logo {
      padding: 0.5em 0;
      position: absolute;
      top: 20%;
      left: 50%;
      transform: translateX(-50%);
    }

    .hideLogo {
      display: none;
    }

    #inputSearch {

      width: 100%;

      z-index: 5;
    }

    .mobileHide {
      display: none;
    }

    .desktopHide {
      display: block;
    }



    .burger {
      display: block;
      margin-right: auto;
      cursor: pointer;
    }

    .nav-active {
      transform: translateY(0%);
    }

    .bottomLinks {
      padding: 0.5em 0;
      border-top: 1px solid black;
      width: 100%;
      position: absolute;
      bottom: 2%;
      @include flexJust;
      justify-content: start;

      a {
        padding: 0 1em;
        color: black;
      }

      img {
        padding: 0 0 1.5em 1em;
      }
    }
  }

  /* ==============================HOme page===================================*/

  .homeBanner {
    flex-direction: column;
    position: relative;
    




    .homeInnerBanner {

      position: absolute;
      // background-image: linear-gradient(to right, rgba(229,229,231,0.4));
      background-image: linear-gradient(to bottom, rgba(299, 299, 231, 0), rgba(299, 299, 231, 1));
      // background-color: rgba(229,229,231,0.4);
      opacity: 0.5;
      bottom: 0;
      width: auto;

      div {
        padding: 0.5;


        h2 {
          /* @extend .display-m-24-2;*/
          width: 100%;
          text-align: center;
        }

        p {
          text-align: center;
        }

        button {
          margin: auto;
          margin-bottom: 0.25em;
        }

        .homeBannerImg {
          background-image: url(../Assets/homepage_1.jpg);
          background-size: cover;
        }
      }
     
    }
    img{
      width: 100%;
      height: 90vh;
    }

  }

  .mobileHide2 {
    display: none;
  }

  .fourImageOuter {
    
    // height: 100%;
    margin: auto;
    // width: 90%;
    max-width: 100%;
    text-align: center;
    margin-bottom: 4em;

    .fourImageInner {
      flex-direction: column;
     
      width: 90%;
      max-width: 100%;
      margin:  auto;
      


    }
  }


  /* ==============================FourImage===================================*/
  .fourImgComp {
    margin: 0;
    margin-top: 3em;
    position: relative;
  

    img {
      width: 300px;
    }

    .fourImgBanner {
      width: 100%;
      position: absolute;
      bottom: -7%;

    }
  }

  /* ==============================Take off Banner starts=================== */

  .takeoffOuter {
    flex-direction: column-reverse;

    .takeoffInner {
      width: 100%;
      text-align: center;


      h2 {
        text-align: center;
        width: 100%;
      }
    }


    img {
      width: 100%;
    }
  }

  /* ==============================Footer Banner starts=================== */
  .footerBanner {
    flex-direction: column-reverse;
    // margin-bottom: 3rem;
    
    
    .standingMan {
     padding: 0;
     width: 100%;

    }

    .footerBannerInner {
      width: 95%;
      
    //   align-items: center;
    margin: -2rem auto 2rem auto;
      
      
    //   h2 {
    //     text-align: center;
    //   }
      p{
        padding-top: 1em;
      }
      button{
        margin: 2em 0;
      }
    }
  }




  /* ==============================Hero Banner===================================*/
  .heroBanner {

    position: relative;
    flex-direction: column-reverse;

    .heroInnerBanner {
      padding: 1em;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateX(-50%);

      margin: auto;

      width: 75%;

      h2 {
        // @extend .display-lmobile-36-;
        color: $white;

        padding: 0 0 1em 0;
      }
    }

    img {
      padding: 1em 1em 0em 1em;
      margin-bottom: 5em;
    }
  }

  /*============================Product List============================*/

  .produFilters {

    padding: 2em 0 1em 0;
    @include flexJust;
    justify-content: space-around;

    div {

      @include flexAlign;

      img {
        padding: 0 0.5em;
      }
    }
  }


  * {
    .ui.cards>.card>.image {
      /* height: 250px; */
      height: 5rem;
      padding: 0;

    }

    .ui.cards>.card>.image>img {

      width: 90%;
    }
  }


  /*============================SideBar============================*/

  .sideBar {
    margin-top: 1em;


    .crossHide {
      display: block;
    }


  }

  .sideBarToggle {
    display: none;
    transition: transform 0.8s ease-in;
    transform: translateX(-140%);

  }

  .sideBar-active {
    transform: translateX(0%);
    display: block;
  }

  /*============================Shopping Cart ============================*/


  .sharingOption {
    display: none;
  }

  .cartBtn {
    text-align: right;
    // margin-left: auto;
    @include flexAlignJust;
    padding: 0
  }

  .sharingOuter {
    @include flexAlignJust;
  }

  .leftSideCart {
    display: flex;
    flex-direction: column;
    text-align: center;
  }



  /*============================Product Details============================*/

  .imgCarousal {
    display: none;
  }

  /* ==============================Footer===================================*/
  .lowerFooter {
    flex-direction: column;
  }
}